.Aside {
    background-color: #df861d;
    color: #dfaa1d;
    grid-area: aside;
}
  
.Aside-table {
  display: table;
  border-spacing: 20px;
  font-size: x-large;
  height: 100%;
}

.Aside thead {
  font-size: 2em;
  height: 20%;
  color: #894a00;
}

.Button {
  vertical-align: middle;
  text-align: center;
  border-radius: 20%;
  -webkit-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
  transition: background-color 0.5s;
}
  


